import style from './SceneHeader.module.css'
import { motion, MotionCanvas, LayoutCamera } from 'framer-motion-3d'
import { Shadow, softShadows, Stats } from '@react-three/drei'
import { useThree, useFrame } from '@react-three/fiber'
import { degToRad } from 'three/src/math/MathUtils'

softShadows()

const SceneHeader = () => {
    return (
        <div className={style.SceneHeader}>
            <Scene />
        </div>
    )
}

const Scene = () => {
    return (
        <MotionCanvas dpr={[1, 2]} shadows>
            <LayoutCamera
                transition={{duration: 5, repeat: Infinity, repeatType: 'reverse'}}
                initial={{x: 10, y: 3, z:4, rotateY: degToRad(90), fov: 15}}
                animate={{x: 10, y: 6, z: 10, rotateY: degToRad(90), fov: 15}}
            />
            <Lights />
            <Geometry />
        </MotionCanvas>
    )
}

const Lights = () => {
    const three = useThree()
    useFrame(() => {
        three.camera.lookAt(0, 0, 0)
    })
    
    return (
        <>
            <Stats />
            <ambientLight intensity={0.2} color='#fff'/>
            <pointLight position={[10, 5, 5]} intensity={.5} color="#6622ff"/>
            <motion.directionalLight
                castShadow
                intensity={1}
                shadow-mapSize-width={8128}
                shadow-mapSize-height={8128}
                shadow-camera-far={30}
                shadow-camera-left={-10}
                shadow-camera-right={10}
                shadow-camera-top={10}
                shadow-camera-bottom={-10}
                initial={{ x: 10, y: 5, z: 5 }}
            />
        </>
    )
}

const Geometry = () => {
    return (
        <>
      <group position={[0, -1.5, -3]}>
        {/* <mesh
          receiveShadow
          castShadow
          rotation-x={-Math.PI / 2}
          position-z={2}
          scale={[4, 20, 0.2]}
        >
          <boxBufferGeometry />
          <meshStandardMaterial color="#222" />
        </mesh> */}
        <mesh
          receiveShadow
          castShadow
          rotation-x={-Math.PI / 2}
          position={[-1.7, 1, 2]}
          scale={[0.5, 4, 4]}
        >
          <boxBufferGeometry />
          <meshStandardMaterial color="#222" />
        </mesh>
        <mesh
          
          receiveShadow
          castShadow
          rotation-x={-Math.PI / 2}
          rotation-z={-Math.PI / 2}
          position={[-1, 1, 2.2]}
          scale={[.5, .1, 4]}
        >
          <boxBufferGeometry />
          <meshStandardMaterial wireframe color="#666" />
        </mesh>
        <mesh
          
          receiveShadow
          castShadow
          rotation-x={-Math.PI / 2}
          rotation-z={-Math.PI / 2}
          position={[-1.2, 1.7, 1.7]}
          scale={[.5, .1, 4]}
        >
          <boxBufferGeometry />
          <meshStandardMaterial color="white" />
        </mesh>
        <mesh
          receiveShadow
          castShadow
          rotation-x={-Math.PI / 2}
          rotation-z={-Math.PI / 4}
          position={[-1, 1, 3.7]}
          scale={[.5, 0.03, 4.5]}
        >
          <boxBufferGeometry />
          <meshStandardMaterial color="#ee3355" />
        </mesh>
      </group>
      <motion.mesh 
      position={[0, .2, 0]}
      transition={{duration: 3, repeat: Infinity, repeatType: 'reverse'}}
      initial={{rotateX: 0, rotateY: 1}}
      animate={{rotateX: 3.1}}
      receiveShadow
      castShadow>
        <lineBasicMaterial color="#000" linewidth={4}/>
        <torusBufferGeometry args={[.5, .3, 128, 100]}/>
        <meshPhysicalMaterial
        //   transparent
        //   opacity={0.9}
        //   wireframe
        //   flatShading
          color="#1188ff"
          clearcoat={1}
          clearcoatRoughness={.5}
        />
        {/* <Shadow
          position-y={-0.79}
          rotation-x={-Math.PI / 2}
          opacity={0.6}
          scale={[0.8, 0.8, 1]}
        /> */}
      </motion.mesh>
    </>
    )
}

export default SceneHeader